@import "utils";
@import "fonts";
@import "typography";

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  background: #fcfcfd
    linear-gradient(
      116deg,
      #56ccf2 0%,
      #56ccf2 77.5%,
      #fcfcfd 77.55%,
      #fcfcfd 100%
    )
    0 0 / 100% 848px no-repeat;
}

.header__content {
  top: 0;
  width: 95%;
  height: 100%;
  position: relative;
  padding: 60px 10% 0;
}

.navigation__menu {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  align-items: center;
}

.navigation-list {
  display: flex;
  justify-content: space-around;
  text-decoration: none;
}

.navigation-list li {
  list-style-type: none;
}

.nav-link {
  text-decoration: none;
  color: #fff;
  display: block;
  padding: 10px 0;
  position: relative;
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: bold;
  line-height: 150%;

  &:focus {
    outline: none;
  }

  @media only screen and (max-width: 770px) {
    width: 100%;
    justify-content: center;
  }

  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    background: #fcc304;
    border-radius: 32px;
    width: 0;
    transition: width 0.3s;
  }

  &:hover {
    &:after {
      width: 100%;
    }
  }
}

.logo {
  color: #fff;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  font-family: Muli, sans-serif;
  transition: all 0.5s;

  &:focus {
    outline: none;
  }

  &:hover {
    transform: scale(1.2);
    color: #dfdedb;
  }
}

.nav {
  width: 55%;
}

.contacts a {
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #56ccf2;
  margin: 0;
  text-decoration: none;
  transition: all 0.3s linear;
}

.hire-link {
  a {
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;

    &:focus {
      outline: none;
    }
  }
}

.agency {
  padding: 30px 0 0 79px;
}

.agency-name {
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 35px;
  line-height: 150%;
  color: #fff;
}

.agency-description {
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 150%;
  color: #fff;
}

.relevant__button {
  height: 56px;
  width: 226px;
  border-radius: 64px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  cursor: pointer;
  transition: background-color 0.4s, color 0.4s;
  outline: none;
  border: 1px solid #fff;
  margin-top: 60px;

  &--white {
    background-color: #fff;
    color: #253757;

    &:hover {
      background-color: #dfb93c;
      color: #fff;
      border: 1px solid #dfb93c;
    }
  }

  &--link {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.slider {
  max-width: 1140px;
  position: relative;
  margin-bottom: 50px;
  margin-top: 90px;

  &__image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    max-height: 640px;
  }

  &__image {
    display: block;
    max-width: 100%;
    width: 100%;
  }

  &__buttons {
    position: absolute;
    display: flex;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fcc304;
    border-radius: 50px;
    overflow: hidden;

    @media only screen and (max-width: 1246px) {
      left: 50%;
      top: initial;
      bottom: 0;
      transform: translate(-50%, 50%);
    }
  }

  &__prev-button,
  &__next-button {
    padding: 0;
    height: 64px;
    width: 81px;
    background: transparent;
    border: 1px solid transparent;
    cursor: pointer;
    outline: none;
  }

  &__next-button {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    transition: background-color 0.3s;

    &:hover {
      background-color: #e98b21;
    }

    &:active {
      transform: translateX(2px);
    }
  }

  &__prev-button {
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    transition: background-color 0.3s;

    &:hover {
      background-color: #e98b21;
    }

    &:active {
      transform: translateX(-2px);
    }
  }

  &__info {
    position: absolute;
    left: 98px;
    bottom: 86px;
    max-width: 264px;
  }

  &__text {
    font-size: 20px;
    line-height: 1.4;
    font-weight: 900;
    color: #fff;
    margin: 0;

    @media only screen and (max-width: 600px) {
      font-size: 14px;
      font-weight: 700;
    }
  }
}

.soc {
  list-style: none;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  right: -83px;
  font-size: 13px;
  font-weight: 800;
  line-height: 1.5;
  letter-spacing: 2px;
  font-family: Muli, sans-serif;
  font-style: normal;

  @media only screen and (max-width: 1250px) {
    display: none;
  }

  &-item {
    &:not(:first-child) {
      margin-top: 85px;
    }
  }

  &-link {
    text-decoration: none;
    color: #253757;
    display: block;
    padding: 0 5px;
    transition: color 0.3s;

    &:focus {
      outline: none;
    }

    &--facebook {
      &:hover {
        color: #4271d6;
      }
    }

    &--twitter {
      &:hover {
        color: #1da1f2;
      }
    }

    &--instargam {
      &:hover {
        color: #e1306c;
      }
    }
  }
}

.title {
  font-size: 16px;
  font-weight: 800;
  line-height: 1.5;
  color: #56ccf2;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0 0 19px;
  display: flex;
  align-items: center;

  &:before {
    content: "";
    display: block;
    width: 21px;
    height: 21px;
    margin-right: 16px;
    background: url("../images/dotted-blue.svg") 50% 50% no-repeat;
  }

  &--without-icon {
    font-size: 12.6px;
    margin-bottom: 4px;

    &:before {
      content: none;
    }
  }

  &--message {
    margin: 0 0 88px;
  }

  &--secondary {
    color: #fff;

    &:before {
      background: url("../images/dotted-white.svg") 50% 50% no-repeat;
    }
  }
}

